import React from "react"
import Rainbow from "./rainbow"
import layoutStyles from "./layout.module.css"

const Cover = props => {
    return (
      <div className={`${layoutStyles.cover} full-viewport-width`}>
        { props.bar && <Rainbow/> }
        { props.src && <img src={props.src} alt="cover"/>}
        { props.text && <h1 className="d-none d-sm-block">{props.text}</h1> }
      </div>
    )
};

Cover.defaultProps = {
  bar: true,
  src: null,
  text: null
}

export default Cover;
